import { Component, OnInit, ViewChild } from '@angular/core';
import { Location } from "@angular/common";
import {ActivatedRoute} from '@angular/router';
import { BookService } from '../../services/book/book.service';
import { AlertController, Platform } from '@ionic/angular';

@Component({
  selector: 'app-book',
  templateUrl: './book.page.html',
  styleUrls: ['./book.page.scss'],
})
export class BookPage implements OnInit {
    
    
    public book:any;
    public loading:boolean;
    public currentPage:number;
    public audioPlaying:boolean;
    @ViewChild('audioPlayer') audio: any;
    
    constructor(private bookService:BookService, private route:ActivatedRoute, 
        private alertCtrl: AlertController, private location: Location, public platform: Platform) { 
        
        this.loading = true;
        
        let bookId = this.route.snapshot.params['id'];
        
                
        this.book = {id:bookId, pages:[]};
        
        this.getBook();
    
        
    }

    ngOnInit() {
    }
    
    public getBook(){
        this.bookService.getBook(this.book.id).then((data) => {
            console.log(data);
            this.book = data;
            this.loading = false;
        }).catch(() => {
            this.loading = false;
        })
    }
    
    public startReading(){
        this.currentPage = 0;
		if (!this.platform.is("ios")){
			//autoplay disabled for ios
			this.playAudio();
		}
        this.book.started = true;
    }
    
    public nextPage(){
        this.currentPage += 1;
		if (!this.platform.is("ios")){
			this.playAudio();
		}
    }
    
    public previousPage(){
        this.currentPage -= 1;
    }
    
    public playAudio(){
        let element:any = document.getElementById("pageaudio" + this.currentPage);
        element.play();
        this.audioPlaying = true;
        element.addEventListener('ended',() => {
            this.audioPlaying = false;
        });        
    }
    
    public pauseAudio(){
        let element:any = document.getElementById("pageaudio" + this.currentPage);
        element.pause();
        this.audioPlaying = false;
    }
    
    public getTipPosition(positionText){
        
        let positionItems = positionText.split(",");
        
        return {top:positionItems[0] + "%", left:positionItems[1] + "%"}
        
        
    }
    
    public openTip(tip){
        let alert = this.alertCtrl.create({
            header: 'Tip',
            message: tip,
            buttons: [
                {
                    text: 'Close',
                    role: 'cancel',
                    handler: data => {
                    }
                }    
            ]
        }).then((el) => {
            el.present();
        });          
    }
    
    public closeBook(){
        this.location.back();
    }
    
}
